import { BareClient, BareResponseFetch } from '@mercuryworkshop/bare-mux';
import { rewriteBlobby } from '../rewriters/url';

class EnvadeServiceWorker {
  client: BareClient;

  constructor() {
    this.client = new BareClient();
  }

  shouldRoute({ request }: FetchEvent) {
    return request.url.startsWith(location.origin + self.__envade$config.prefix);
  }

  async handleFetch({ request }: FetchEvent) {
    let rewrittenUrl = self.__envade$rewrite.url.unwriteUrl(request.url);
    try {
      console.log('Fetching URL:', rewrittenUrl);

      if (rewrittenUrl.startsWith('data:') || rewrittenUrl.startsWith('blob:')) {
        // blobby my pet :)
        if (rewrittenUrl.startsWith('blob:')) {
          rewrittenUrl = rewriteBlobby(rewrittenUrl);
        }
        console.log('Handling data or blob URL:', rewrittenUrl);
        const res = await fetch(rewrittenUrl, {});
        return new Response(res.body, {
          status: res.status,
          statusText: res.statusText,
          headers: res.headers,
        });
      }

      const reqHeaders = new Headers(request.headers);
      const target = new URL(rewrittenUrl);

      reqHeaders.set('host', target.host);
      reqHeaders.set('origin', target.origin);

      let res: BareResponseFetch = await this.client.fetch(rewrittenUrl, {
        method: request.method,
        body: request.body,
        headers: reqHeaders,
        credentials: 'omit',
        mode: request.mode === 'cors' ? request.mode : 'same-origin',
        cache: request.cache,
        redirect: request.redirect,
        // @ts-expect-error
        duplex: 'half',
      });

      const rewrittenHeaders: Headers = await self.__envade$rewrite.headers(
        res.headers,
        rewrittenUrl,
      );

      let body: ReadableStream | string = res.body;

      if (res.body) {
        switch (request.destination) {
          case 'iframe':
          case 'document':
            console.log('Rewriting HTML');
            body = await self.__envade$rewrite.html(await res.text(), rewrittenUrl);
            break;
          case 'style':
            console.log('Rewriting CSS');
            body = await self.__envade$rewrite.css(await res.text(), rewrittenUrl);
            break;
          case 'script':
          case 'worker':
          case 'sharedworker':
            console.log('Rewriting JS');
            body = await self.__envade$rewrite.javascript(await res.text(), rewrittenUrl);
            break;

          default:
            body = res.body!;
            break;
        }
      }

      if (['document', 'iframe'].includes(request.destination)) {
        const disposition = rewrittenHeaders.get('content-disposition') || '';
        if (!/^\s*(inline|attachment);\s*filename=/i.test(disposition)) {
          const type = disposition.startsWith('attachment') ? 'attachment' : 'inline';
          const filename = res.finalURL?.split('/').pop() ?? 'download';
          rewrittenHeaders.set(
            'content-disposition',
            `${type}; filename=${JSON.stringify(filename)}`,
          );
        }
      }

      return new Response(body, {
        status: res.status,
        statusText: res.statusText,
        headers: rewrittenHeaders,
      });
    } catch (err: any) {
      return this.renderError(err, rewrittenUrl);
    }
  }

  renderError(error: Error, url: string): Response {
    console.error('[SW] Error', error, 'for URL', url);
    const html = `<!DOCTYPE html><html lang="en"><head><meta charset="UTF-8"><title>Envade â€“ Error</title><meta name="viewport" content="width=device-width, initial-scale=1.0"><style>body{font-family:sans-serif;background:#f9fafb;margin:0;padding:2em;color:#111}.container{max-width:600px;margin:auto;background:white;padding:2em;border-radius:8px;box-shadow:0 8px 24px rgba(0,0,0,0.05)}h1{font-size:1.5rem;color:#e11d48}pre{background:#f3f4f6;padding:1em;border-radius:6px;overflow-x:auto}.url{color:#1e3a8a;margin-bottom:1em;font-size:.95em;word-break:break-all}</style></head><body><div class="container"><h1>Something went wrong</h1><div class="url">URL: ${url}</div><div><strong>${error.message || 'Unknown error'}</strong></div><pre>${error.stack || 'No stack trace available'}</pre></div></body></html>`;
    return new Response(html, {
      status: 500,
      headers: { 'Content-Type': 'text/html' },
    });
  }
}

self.EnvadeServiceWorker = EnvadeServiceWorker;
