function rewriteUrl(url: string, origin?: string | URL): string {
  url =
    typeof url === 'string'
      ? url
      : url != null && typeof (url as any).toString === 'function'
        ? (url as any).toString()
        : '';

  if (
    url.startsWith(location.origin + self.__envade$config.prefix) ||
    url.startsWith(self.__envade$config.prefix)
  ) {
    return url;
  }

  if (url.startsWith('about:')) {
    console.log('Rewriting about URL:', url);
    // todo: handle about: URLs
    return url;
  }

  if (url.startsWith('javascript:')) {
    return self.__envade$rewrite.javascript(url, origin);
  }

  if (url.startsWith('data:')) {
    return location.origin + self.__envade$config.prefix + url;
  }

  if (url.startsWith('blob:')) {
    const afterUrl = url.substring(url.lastIndexOf('/') + 1);
    return (
      location.origin +
      self.__envade$config.prefix +
      'blob:' +
      self.scope$location.origin +
      '/' +
      afterUrl
    );
  }

  const base = origin
    ? new URL(typeof origin === 'string' ? self.__envade$rewrite.url.unwriteUrl(origin) : origin)
    : location;

  const resolved = new URL(url, base instanceof URL ? base : base.href).href;
  return (
    location.origin + self.__envade$config.prefix + self.__envade$config.codec.encode(resolved)
  );
}

function unwriteUrl(writtenUrl: string): string {
  if (!writtenUrl.startsWith(self.__envade$config.prefix)) {
    const index = writtenUrl.indexOf(self.__envade$config.prefix);
    if (index !== -1) {
      writtenUrl = writtenUrl.slice(index);
    }
  }

  if (writtenUrl.startsWith(self.__envade$config.prefix + 'blob:')) {
    return writtenUrl.substring(self.__envade$config.prefix.length);
  }

  if (writtenUrl.startsWith(self.__envade$config.prefix + 'data:')) {
    return writtenUrl.substring(self.__envade$config.prefix.length);
  }

  if (writtenUrl.startsWith('mailto:') || writtenUrl.startsWith('about:')) {
    return writtenUrl;
  }

  if (writtenUrl.startsWith(self.__envade$config.prefix)) {
    writtenUrl = new URL(writtenUrl, location.origin).pathname.replace(
      new RegExp(`^${self.__envade$config.prefix}`),
      '',
    );
  }
  return self.__envade$config.codec.decode(writtenUrl);
}

export function rewriteBlobby(url: string) {
  // nice blobby
  const bloburl = new URL(url.substring('blob:'.length));
  return `blob:${location.origin}${bloburl.pathname}`;
}

const url = { rewriteUrl, unwriteUrl };

export { url };
